@font-face {
  font-family: 'ubuntu' ;
  src: url(../fonts/Ubuntu-Regular.ttf);
}
body{
  margin: 0;
  padding: 0;
  background-color: var(--primery);
  font-family: 'ubuntu';
  font-weight: bold;
}
textarea{
  font-family: 'ubuntu';
}
:root{
  --primery: #ffffff;
  --secondery: #d3d3d3;
  --specialy: #0a00bb
}
.sec-logo-container{
  display: inline-flex;
  margin-top: 4%;
  align-items: center;
  justify-content: center;
}
.sec-logo{
  width: 10%;
  margin: 15px 0px;
}
#Homepage{
  display: flex;
  align-content: center;
  justify-content: center;
  align-items: center;
}
.sections-container{
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  width: 90%;
  margin:auto;
  min-height: 400px;
}
.inner-title{
  text-align: center;
  background-color: var(--specialy);
  color: var(--primery);
  margin: 15px;
  border-radius: 10px;
  width: 100%;
}
.inner-content{
  background-color: var(--secondery);
  border-radius: 10px;
  padding: 50px;
}
/*Illustration*/
.Illustration-container{
  display: flex;
  align-items: center;
  flex-direction: column;
}
/*Animations*/
.Animations-container{
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 90%;
}
  /* Modeling */
.Modeling-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 65%;
  }
  .iframe{
    display: flex;
    align-items: center;
    width: 100%;
    margin-bottom: 30px;
  }
.Modeling-3D{
    height: 65vh;
    box-shadow: 5px black;
    width: 100%;
  }
  /* Music */
  .Music-container{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
.rhap_container{
  box-shadow: none !important;
  background-color: transparent !important;
}
  .rhap_container svg{
    color: black !important;
  }
  .rhap_progress-indicator{
    background-color: black !important;
    width: 12px !important;
    height: 12px !important;
    top: -4px !important;
  }
  .rhap_volume-indicator{
    background-color: black !important;
  }
  .rhap_progress-filled{
    background-color: black !important;
  }
  /*Voice*/
  .Voice-container{
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  /*Virtual*/
  .VirtualReality-container{
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  /*Augmented*/
  .AugmentedReality-container{
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  /*Game*/
  .Game-container{
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  /* Contact */
  .contact-container{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    background: var(--secondery);
    border-radius: 10px;
    padding-top: 15px;
    align-items: flex-end;
  }
  .img-section-container{
    width: 45%;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center
  }
  .section-img{
    width: 80%;
  }
  .info-container{
    display: flex;
    flex-direction: column;
    width: 40%;
  }
  .phone-number{
    display: flex;
    align-items: center;
    margin: auto;
  }
  .contact-info{
    font-size: large;
    margin: auto;
  }
  .contact-icon{
    width: 35px;
    height: 35px;
    margin-right: 15px;
  }
  .email{
    display: flex;
    align-items: center;
    margin: auto;
  }
  .input-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 15px;
    align-items: flex-start;
  }
  .text-input{
    background-color: transparent;
    width: 100%;
    border: solid 1px #000000;
    height: 30vh;
    text-align: left;
    border-radius: 15px;
    transition: ease-in .8s;
    font: 'ubuntu' 20px;
    color: #000000;
    padding: 10px 15px;
    box-sizing: content-box;
    margin: 15px 0;
    resize: none;
  }
  .text-input:focus{
    border-radius: 0;
    outline: none;
    font-size: 20px;
  }
  .text-input:focus::placeholder{
    color: transparent;
  }
  .text-input::placeholder{
    color: #000000;
    font-size: 15px;
  }
  .submit-input{
    width: 120px;
    height: 45px;
    font-family: 'Roboto', sans-serif;
    font-size: 11px;
    text-transform: uppercase;
    letter-spacing: 2.5px;
    font-weight: 500;
    color: #000;
    background-color: #fff;
    border: none;
    border-radius: 45px;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease 0s;
    cursor: pointer;
    outline: none;
  }
  .submit-input:hover{
    background-color: var(--specialy);
    box-shadow: 0px 15px 20px #1b0058;
    color: #fff;
    transform: translateY(-7px);
  }

  /* responsive */
  @media screen and (max-width:1200px) {
    .sec-logo-container{
      display: flex;
      justify-content: center;
      margin:15px
    }
    .sec-logo{
      width: 20%;
    }
    .img-section-container {
      width: 60%;
  }
  .iframe{
    width: auto;
  }
  .Modeling-3D{
    width: auto;
    height: auto;
  }
  .contact-container{
      flex-direction: column;
      align-items: center;
      width: 60%;
    }
    .info-container{
      width: 60%;
      padding-top: 30px;
    }
    .input-container{
      align-items: center;
    }
    .contact-info{
      font-size: medium;
    }
    .contact-icon{
      width: 30px;
      height: 30px;
    }
    .text-input{
      width: 150%;
    }
  }
  @media screen and (max-width:480px) {
    .contact-info{
      font-size: small;
    }
    .contact-icon{
      width: 25px;
      height: 25px;
    }
    .contact-container{
      flex-direction: column;
      align-items: center;
    }
    .info-container{
      width: 50%;
      align-items: center;
    }
    .sec-logo{
      width: 25%;
    }
    .section-img{
      width: 100%;
    }
    .phone-number{
      margin: 0;
    }
    .email{
      margin: 0;
    }
    .input-container{
      align-items: center;
    }
    .text-input {
      width: 150%;
  }
    .text-input::placeholder{
      font-size: 12px;
    }
    .text-input:focus{
      font-size: 12px;
    }
  }
