@font-face {
    font-family: 'ubunt' ;
    src: url(../fonts/Ubuntu-Regular.ttf);
}
  body{
    margin: 0;
    padding: 0;
    background-color: var(--primery);
    font-family: 'ubunt';
    font-weight: bold;
  }
  
  :root{
    --primery: #ffffff;
    --secondery: #d3d3d3;
    --specialy: #0a00bb
  }
  .bio-cont{
    display: flex;
    flex-direction: column;
    width: 95%;
    background-color: var(--specialy);
    color: white;
    border-radius: 5px;
    margin: 10px;
  }
  .bio-title{
    text-align: center;
    margin: 10px 0px 5px 5px;
  }
  .bio-text{
    padding: 0px 15px;
    text-align: center;
  }
.names{
  text-align: center;
  }
.About-container{
    display: flex;
    align-items: center;
    flex-direction: column;
    margin: auto;
}
.nariman{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: var(--secondery);
  width: 95%;
  border-radius: 5px;
}
.bios-info{
  padding: 5px 25px;
}
.nariman-img{
  width: 40%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 25px 0px;
}
.nariman-img img{
  width: 50%;
  border-radius: 15px;
}
.nariman-bio{
  width: 60%;
  margin: 25px;
  border-left: var(--specialy) solid 3px;
}
.mahdiar{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: var(--primery);
  width: 95%;
  border-radius: 5px;
}
.mahdiar-img{
  width: 40%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15px 0px;
}
.mahdiar-img img{
  width: 40%;
  border-radius: 15px;
}
.mahdiar-bio{
  width: 60%;
  margin: 25px;
  border-left: var(--specialy) solid 3px;
}
.hadi{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: var(--secondery);
  width: 95%;
  border-radius: 5px;
}
.hadi-img{
  width: 40%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15px 0px;
}
.hadi-img img{
  width: 40%;
  border-radius: 15px;
}
.hadi-bio{
  width: 60%;
  margin: 25px;
  border-left: var(--specialy) solid 3px;
}
.nastaran{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: var(--primery);
  width: 95%;
  border-radius: 5px;
}
.nastaran-img{
  width: 40%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15px 0px;
}
.nastaran-img img{
  width: 80%;
  border-radius: 30px;
  padding: 10px;
}
.nastaran-bio{
  width: 60%;
  margin: 25px;
  border-left: var(--specialy) solid 3px;
}
.sona{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: var(--secondery);
  width: 95%;
  border-radius: 5px;
}
.sona-img{
  width: 40%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px 0px;
}
.sona-img img{
  width: 50%;
  border-radius: 15px;
}
.sona-bio{
  width: 60%;
  margin: 25px;
  border-left: var(--specialy) solid 3px;
}
/*respons*/
@media screen and (max-width:1200px) {
  .nariman-img img{
    width: 50%;
  }
  .nariman-img{
    padding: 50px 0px;
  }
  .mahdiar-img img{
    width: 50%;
  }
  .mahdiar-img{
    padding: 50px 0px;
  }
  .hadi-img img{
    width: 50%;
  }
  .nastaran-img img{
    width: 75%;
  }
  .sona-img img{
    width: 75%;
  }
  
}

@media screen and (max-width:480px) { 
  .bio-cont{
    font-size: smaller;
  }
  .nariman{
    flex-direction: column;
  }
  .nariman-img{
  width: 85%;

  }
.nariman-bio{
  width: 87%;
  margin: 0px;
  font-size: smaller;
  border: none;
  }
  .mahdiar{
    flex-direction: column;
  }
  .mahdiar-img{
    width: 85%;
  }
  .mahdiar-bio{
    width: 87%;
    margin: 0px;
    font-size: smaller;
    border: none;
  }
  .hadi{
    flex-direction: column;
  }
  .hadi-img{
    width: 85%;
  }
  .hadi-bio{
    width: 87%;
    margin: 0px;
    font-size: smaller;
    border: none;
  }
  .nastaran{
    flex-direction: column;
  }
  .nastaran-img{
    width: 85%;
  }
  .nastaran-bio{
    width: 87%;
    margin: 0px;
    font-size: smaller;
    border: none;
  }
  .sona{
    flex-direction: column;
  }
  .sona-img{
    width: 85%;
  }
  .sona-bio{
    width: 87%;
    margin: 0px;
    font-size: smaller;
    border: none;
  }
  
}