@font-face {
  font-family: 'ubunt' ;
  src: url(../src/fonts/Ubuntu-Regular.ttf);
}
body{
  margin: 0;
  padding: 0;
  background-color: var(--primery);
  font-family: 'ubunt';
  font-weight: bold;
}

:root{
  --primery: #ffffff;
  --secondery: #d3d3d3;
  --specialy: #0a00bb
}
.App{
  width: 100vw;
  height: 95vh;
}
#About{
  display: inline-block;
  width: 20%;
}
.Abouttext {
  opacity: 0;;
  width: 100px;
  color: var(--primery);
  text-align: center;
  background-color: var(--specialy);
  border-radius: 4px;
  padding: 5px;
  position: absolute;
  z-index: 1;
  transition: ease-in-out 0.5s;
}
.logo-container{
  display: inline-flex;
  margin: 3% 0px;
  margin-left: 3%;
}
#About:hover  .Abouttext{
  opacity: 1;
  margin-left: 15%;
}
#About:hover .logo{
  transform: rotateY(360deg);
}
.logo{
  width: 55%;
  transition: ease-in-out 0.8s;
}
.Menue-container{
  width: 95%;
  justify-content: center;
  background-color: var(--secondery);
  margin: auto;
  height: 63%;
  display: flex;
  align-content: space-around;
  justify-content: center;
  align-items: flex-end;
  margin-top: auto;
  border-radius: 15px;
}
.options-container{
  list-style: none;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  width: 50%;
  margin: auto;
}
.img-container{
  display: flex;
  justify-content: center;
  width: 40%;
}
.img{
  width: 75%;
}
.options{
  width: auto;
  height: auto;
  background-color: var(--primery);
  margin: 15px;
  cursor: pointer;
  text-align: center;
  color: #000000;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'ubunt';
  font-weight: lighter;
  box-shadow: rgba(0, 0, 0, 0.45) 0px 25px 20px -20px;
  transition: all 0.2s ease-in-out;
  border-radius: 3px;
}
.options:hover{
  box-shadow: rgba(0, 0, 0, 0.6) 0px 30px 25px -20px;
}
.options:active{
  box-shadow: 0px 0px 0px;
  transform: scale(0.9);
}
.option-title{
  content: '';
  position: absolute;
  opacity: 0;
  display: flex;
  justify-content: center;
  text-align: center;
  transition: ease-in 0.2s;
}
.option-icon{
  border-radius: 5px;
  width: 100%;
  height: 100px;
  transition: ease-in-out 0.2s;
  border-radius: 3px;

}
.option-icon img{
  height: 100%;
  transition: ease-in 0.2s;
  border-radius: 3px;
}
.options:hover .option-icon{
  opacity: 0.3;
}
.options:hover .option-title{
  opacity: 1;
}
.social-container{
display: flex;
justify-content: center;
margin-top: 15px;
}
.social-icon{
  width: 30px;
  margin: 10px;
  transition: ease-in 0.2s;
}
#twitter:hover{
  filter: invert(48%) sepia(95%) saturate(355%) hue-rotate(144deg) brightness(91%) contrast(102%);
}
#facebook:hover{
  filter: invert(14%) sepia(61%) saturate(7285%) hue-rotate(246deg) brightness(84%) contrast(94%);
}
#youtube:hover{
  filter: invert(16%) sepia(83%) saturate(4575%) hue-rotate(2deg) brightness(107%) contrast(104%);
}
#instagram:hover{
  filter: invert(18%) sepia(62%) saturate(4240%) hue-rotate(330deg) brightness(93%) contrast(95%);
}

/* responsive */

@media screen and (max-width: 2780px) {
  .options-container{
    width : 30%;
    margin: auto;
  }
  
}
@media screen and (min-width:1920px) and (max-width:2200px){
  .options-container{
    width : 35%;
    margin: auto;
  }
  
}
@media screen and (min-width:1500px) and (max-width:1920px){
  .options-container{
    width: 45%;
  }
  .img{
    width: 75% ;
  }
}
@media screen and (min-width:1200px) and (max-width:1500px){
  .options-container{
    width: 55%;
  }
  .img{
    width: 75% ;
  }
}

@media screen and (max-width:1200px) {
  .Menue-container{
    display: flex;
    align-items: center;
    height: auto;
    flex-direction: column;
    clip-path: none;
  }
  .img-container{
    margin:10px 0px;
  }
  #About:hover  .Abouttext{
    opacity: 0;
  }
  #About:hover .logo{
    transform: none;
  }
  #About{
    display: flex;
    justify-content: center;
    width: 40%;
  }
  .options-container{
    width: 65%;
    margin: 30px 0px;
  }
  .option-icon{
    height: 75px;
  }
  .option-title{
    opacity: 1.5 !important;
    display: flex;
    width: 15%;
    height: 50px;
    align-items: center;
  }
  .option-icon img{
  opacity: 0.3 !important;
  }
}
@media screen and (max-width:700px) {
  .Menue-container{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: auto;
  height: auto;
}
.logo-container{
  display: flex;
  justify-content: center;
  margin:15px
}
#About:hover  .Abouttext{
  opacity: 0;
}
#About:hover .logo{
  transform: none;
}
#About{
  display: flex;
  justify-content: center;
  width: 40%;
}
.img-container{
  margin: 25px 0px;
}
.img{
  width: 75%;
}
.options-container{
  display: flex;
  flex-direction: row;
  align-content: center;
  flex-wrap: wrap;
  margin: 10px 0px;
  width: 75%;
}
.options{
  margin: 10px 15px;
}
.option-title{
  opacity: 1.5 !important;
  font-size: small;
}
.option-icon{
  height: 60px;
}
.option-icon img{
opacity: 0.3 !important;
height: 60px;
}
}
